import react from 'react';

function Cartmenu() {
    return(
        <div className="cartmenu">
          <div className="carthead">
            <ul>
              <li>Food</li>
              <li>Qty</li>
              <li>Price</li>
              <li>Total</li>
            </ul>
          </div>
          <div className="ullist">
            <ul className="food c">
              <li>tacos</li>
              <li>5</li>
              <li>2500</li>
              <li>12500</li>
            </ul>
          </div>
          
          <div id="total">
            <hr />
            <h5>Total</h5>
          </div>
        </div>
    )
}

export default Cartmenu